<template>
  <div class="pt50">
    <video-player
      class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
    ></video-player>
    <van-collapse v-model="activeNames" accordion>
      <van-collapse-item size="large" value="" name="1">
        <template #title>
          <div>
            
            <span class="size12" style="color:rgb(255, 85, 51)">8.0</span>
            <span class="size12"></span>
          </div>
        </template>
        <div class="title"></div>
        <div class="mb5">Iconfonticon</div>
      </van-collapse-item>
      <van-collapse-item title="" name="2">
        <img class="viewImg" v-for="(img,index) in imageList" :key="index" :src="img" />
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import pic1 from "../../assets/shortVideo/1.jpg";
import pic2 from "../../assets/shortVideo/2.jpg";

export default {
  data() {
    return {
      playerOptions: {
        playbackRates: [0.5, 1.0, 1.5, 2.0], //
        autoplay: false, //true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "", //git
            src:
              "https://storage.jd.com/about/big-final.mp4?Expires=3730193075&AccessKey=3LoYX1dQWa6ZXzQl&Signature=ViMFjz%2BOkBxS%2FY1rjtUVqbopbJI%3D" //url
          }
        ],
        poster: "/static/2.1.1.jpg", //
        // width: document.documentElement.clientWidth, //
        notSupportedMessage: "", //Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //
        }
      },
      activeNames: ["1"],
      imageList: [pic1, pic2]
    };
  },
  methods: {}
};
</script>
<style lang="less" scoped>
.video-js .vjs-icon-placeholder {
  width: 100%;
  height: 100%;
  display: block;
}
.vjs-big-play-button {
  width: 100px !important;
  height: 60px !important;
}
.size12 {
  font-size: 12px;
}
.title {
  font-size: 14px;

  margin-bottom: 5px;
}
.viewImg {
  height: 150px;
  width: 100%;
  margin-bottom: 5px;
}
</style>



