import axios from "axios";
import env from "./env";
// import qs from "qs";
import router from "../router";
import { Toast } from "vant";

axios.defaults.timeout = 10 * 1000;
// cookie
axios.defaults.withCredentials = false;
// 
// axios.defaults.headers.common["Authorization"] = store.getters.token;
// 
axios.defaults.baseURL = env.baseURL2;
// 
axios.interceptors.request.use(
  config => {
    // loading
    Toast.loading({
      message: "...",
      duration: 0,
      forbidClick: true,
      loadingType: "spinner"
    });
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);
function remove() {
  localStorage.removeItem("token");
}
axios.interceptors.response.use(
  response => {
    Toast.clear();
    return response;
  },
  error => {
    Toast.clear();
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.Message = "(400)";
          break;
        case 401:
          remove();
          router.push("/login");
          break;
        case 403:
          err.Message = "(403)";
          break;
        case 404:
          err.Message = "(404)";
          break;
        case 408:
          err.Message = "(408)";
          break;
        case 500:
          err.Message = "(500)";
          break;
        case 501:
          err.Message = "(501)";
          break;
        case 502:
          err.Message = "(502)";
          break;
        case 503:
          err.Message = "(503)";
          break;
        case 504:
          err.Message = "(504)";
          break;
        case 505:
          err.Message = "HTTP(505)";
          break;
        default:
          err.Message = `(${err.response.status})!`;
      }
    } else {
      err.Message = "!";
    }
    Toast(err.Message);
    return Promise.reject(error);
  }
);

export function post(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .post(url, params)
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params
      })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}
export function put(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .put(url, params)
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}
export function dlt(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .delete(url, {
        params: params
      })
      .then(res => {
        resolve(res.data);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}



